"use client";
import Image from "next/image";

export default function MenuItemModal({ item, onClose }) {
  if (!item) return null;

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-white rounded-xl p-6 w-96 relative">

        <button
          onClick={onClose}
          className="absolute top-2 right-2 text-lg"
        >
          ✕
        </button>

        <Image
          src={`/menu${item.image}`}
          alt={item.name}
          width={400}
          height={300}
          className="rounded-xl object-cover"
        />

        <h2 className="text-2xl font-bold mt-4">{item.name}</h2>

        <div className="mt-3 text-sm">
          <p><strong>Calories:</strong> {item.calories}</p>
          <p><strong>Protein:</strong> {item.macros.protein}g</p>
          <p><strong>Carbs:</strong> {item.macros.carbs}g</p>
          <p><strong>Fat:</strong> {item.macros.fat}g</p>
        </div>

      </div>
    </div>
  );
}
